import React, { Component } from "react";
import { Navbar, NavbarBrand, Nav, NavbarToggler, NavItem, Collapse, Jumbotron } from 'reactstrap';
import {NavLink} from 'react-router-dom';


class Header extends Component {
    constructor(props) {
        super(props);
        this.state= {
            isNavOpen: false,
        }
        this.toggleNav=this.toggleNav.bind(this);
    }
    toggleNav() {
        this.setState({
            isNavOpen : !this.state.isNavOpen
        })
    }
    render() {
        return (
            //this is react fragment. either we write this or <React.fragment><React.fragment/>
            <>
                <Navbar dark expand="md">
                    <div className="container">
                        <NavbarToggler onClick={this.toggleNav} />
                        <NavbarBrand className="mr-auto" href="/">
                            <img src="assets/images/logo.png" height="45" width="90" alt="Restaurant Con Fusion"></img>
                        </NavbarBrand>
                        <Collapse isOpen={this.state.isNavOpen} navbar>
                        <Nav navbar>
                            <NavItem>
                                <NavLink className="nav-link" to="/home">
                                    <span className="fa fa-home fa-lg"></span> Home
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to="/aboutus">
                                    <span className="fa fa-info fa-lg"></span> About Us
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to="/menu">
                                    <span className="fa fa-list fa-lg"></span> Menu
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to="/contactus">
                                    <span className="fa fa-addresscard fa-lg"></span> Contact Us
                                </NavLink>
                            </NavItem>
                        </Nav>
                        </Collapse>
                    </div>
                </Navbar>

                <Jumbotron>
                    <div className="container">
                        <div className="row row-header">
                            <div className="col-12 col-sm-6">
                                <h1>Zubia's Restaurant</h1>
                                <p>We take inspiration from the World's best cuisines, and create a unique fusion experience. <br/>
                                 Our lipsmacking creations will tickle your culinary senses!</p>

                            </div>
                        </div>
                    </div>
                </Jumbotron>
            </>
        );
    }
}

export default Header;